import Vue from 'vue'
import VueRouter from 'vue-router'
import CommonWrapper from '@/components/CommonWrapper'
import PageCover from '@/views/PageCover'
import Page404 from '@/views/Page404'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/index'
  },
  {
    path: '/workitcompare',
    name: 'workitCompare',
    component: () => import(/* webpackChunkName: "workitCompare" */ '../views/PageWorkitCompare.vue')
  },
  {
    path: '/',
    component: CommonWrapper,
    children: [
      {
        path: '/index',
        name: 'index',
        component: PageCover
      }, // 入口页
      {
        path: '/ai0',
        name: 'ai0',
        component: () => import(/* webpackChunkName: "pageAi0" */ '../views/PageAi0.vue')
      },
      {
        path: '/ai1',
        name: 'ai1',
        component: () => import(/* webpackChunkName: "pageAi1" */ '../views/PageAi1.vue')
      },
      {
        path: '/gameMajiongEmpire',
        name: 'gameMajiongEmpire',
        component: () => import(/* webpackChunkName: "gameMajiongEmpire" */ '../views/PageMajiongEmpire.vue')
      }
      // {
      //   path: '/test',
      //   name: 'test',
      //   component: pageTest
      // }
    ]
  },
  {
    path: '*',
    redirect: {
      path: '/404'
    }
  },
  {
    path: '/404',
    component: Page404
  }
]

const router = new VueRouter({
  routes
})

export default router
