<template>
  <section class="comp-wrapper">
    <div class="bed-menu">
      <div class="box-menu">
        <div class="box-menu-item" v-for="(item, index) in menu" :key="item.id">
          <div class="menu-item" :class="{ 'active': item.id === $route.name }" @mouseover="handleHover(item.id)" @mouseout="handleHoverOut(item.id)" @click="handleMenuClick(item, index)">
            {{ item.name }}
            <div class="btn" v-show="item.withBtn && hoverId === item.id" @click="goUrl(item)">GO</div>
          </div>
          <div class="sub-menu" :class="{ 'active': sitem.id === $route.name }" v-show="item.isShowNodes && item.nodes" v-for="(sitem, sindex) in item.nodes" :key="sitem.id" @mouseover="handleHover(sitem.id)" @mouseout="handleHoverOut(sitem.id)" @click="handleMenuClick(sitem, sindex)">
            {{ sitem.name }}
            <div class="btn" v-show="sitem.withBtn && hoverId === sitem.id" @click="goUrl(sitem)">GO</div>
          </div>
        </div>
      </div>
    </div>
    <div class="bed-content">
      <router-view></router-view>
    </div>
  </section>
</template>

<script>
import urlMap from '@/models/UrlMap'

export default {
  data () {
    return {
      hoverId: '', // 目前hover的id
      menu: [
        {
          id: 'index',
          name: '介绍',
          url: {
            path: '/index'
          },
          withBtn: false,
          targetUrl: '', // 直接访问的链接
          isShowNodes: false, // 是否展开子节点
          nodes: [] // 子菜单
        },
        {
          id: 'ai',
          name: '人工智能学习笔记',
          url: '',
          withBtn: false,
          targetUrl: '', // 直接访问的链接
          isShowNodes: false, // 是否展开子节点
          nodes: [
            {
              id: 'ai0',
              name: '0 初识AI',
              url: {
                path: '/ai0'
              },
              withBtn: false,
              targetUrl: '', // 直接访问的链接
              isShowNodes: false, // 是否展开子节点
              nodes: []
            },
            {
              id: 'ai1',
              name: '1 NumPy中dot方法矩阵积算法',
              url: {
                path: '/ai1'
              },
              withBtn: false,
              targetUrl: '', // 直接访问的链接
              isShowNodes: false, // 是否展开子节点
              nodes: []
            }
          ] // 子菜单
        },
        // {
        //   id: 'test',
        //   name: '测试页面',
        //   url: {
        //     path: '/test'
        //   },
        //   withBtn: false,
        //   targetUrl: '', // 直接访问的链接
        //   isShowNodes: false, // 是否展开子节点
        //   nodes: [] // 子菜单
        // },
        {
          id: 'games',
          name: '游戏作品',
          url: '',
          withBtn: false,
          targetUrl: '',
          isShowNodes: false, // 是否展开子节点
          nodes: [
            {
              id: 'gameMajiongEmpire',
              name: 'Majiong Empire on Web',
              url: {
                name: 'gameMajiongEmpire'
              },
              withBtn: true,
              targetUrl: urlMap.url.gameMajiongEmpire,
              isShowNodes: false, // 是否展开子节点
              nodes: []
            }
          ]
        }
      ]
    }
  },

  methods: {
    handleHover (hoverId) {
      if (hoverId) {
        this.hoverId = hoverId
        // console.log(this.hoverId)
      }
    },
    handleHoverOut (hoverId) {
      if (hoverId) {
        this.hoverId = ''
      }
    },
    handleMenuClick (item, index) {
      if (item) {
        if (item.nodes && item.nodes.length) {
          // 拥有子目录，点击展开或者收缩目录
          // NOTE 现在来说，目录层级不会超过2级，所以操作的肯定是root层级的节点
          this.menu[index].isShowNodes = !this.menu[index].isShowNodes
        } else if (item.url) {
          this.goRoute(item)
        }
      }
    },
    goRoute (item) {
      if (item && item.url) {
        this.$router.push(item.url)
      }
    },
    goUrl (item) {
      if (item && item.targetUrl) {
        window.location.href = item.targetUrl
      }
    }
  },

  mounted () {
    // console.log(this.$route)
  }
}
</script>

